import { getUserProfile } from "@/config/redux/action/authAction";
import { getAllPost } from "@/config/redux/action/postAction";
import style from "./style.module.css";
import UserLayout from "@/layout/userLayout";
import { useRouter } from "next/router";
import React, { Children, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

const DashboardComponent = () => {
  const [isToken, setIsToken] = useState(false);

  const router = useRouter();
  const dispatch = useDispatch();
  const authState = useSelector((state) => state.auth);

  useEffect(() => {
    if (localStorage.getItem("token") === null) {
      router.push("/login");
    }
    setIsToken(true);
  });

  useEffect(() => {
    if (isToken) {
      dispatch(getAllPost());
      dispatch(getUserProfile({ token: localStorage.getItem("token") }));
    }
  }, [isToken]);

  return (
    <UserLayout>
      <div className="container">
        <div className="Home_container">
          <div className="Home_sideBar_container">dashboard</div>
          <div className="Home_dashboard_container">myConnection</div>
          <div className="Home_feedBack_container">feedback</div>
        </div>
      </div>
      {Children}
    </UserLayout>
  );
};

export default DashboardComponent;
